// import * as mod from "./mod";
import { dateString, addTr, createColumn } from "./mod";
function addContest(contest, attr = []) {
    let arr = [dateString(contest.start_epoch_second * 1000), contest.title, contest.rate_change];
    return addTr(createColumn(arr), attr);
}
const header = ["Date", "ContestName", "RateChange"];
const local = "./json/contests.json";
const url = "https://kenkoooo.com/atcoder/resources/contests.json";
fetch(local, { mode: 'cors' })
    .then(res => res.json())
    .then(json => {
    json.sort((a, b) => b.start_epoch_second - a.start_epoch_second);
    let table = [];
    table.push("<table>");
    table.push("<thead>");
    table.push(addTr(createColumn(header)));
    table.push("</thead>");
    table.push("<tbody>");
    for (let i = 0; i < json.length; i++) {
        let attr = i % 2 === 0 ? ['class="table-even"'] : [];
        table.push(addContest(json[i], attr));
    }
    table.push("</tbody>");
    table.push("</table>");
    // const cltable = document.querySelector("#root");
    // if (cltable) cltable.innerHTML = table.join("");
    document.querySelector("#root").innerHTML = table.join("");
});
