export function addTr(item, attr = []) {
    let ret = [];
    ret.push(`<tr ${attr.join("")}>`);
    ret.push(item);
    ret.push("</tr>");
    return ret.join("");
}
export function addTd(item, attr = []) {
    let ret = [];
    ret.push("<td>");
    ret.push(item);
    ret.push("</td>");
    return ret.join("");
}
export function createColumn(column) {
    let arr = [];
    for (const item of column) {
        arr.push(addTd(item));
    }
    return arr.join("");
}
export function dateTimeString(epochseconds) {
    const date = new Date(epochseconds);
    const month = ("0" + (date.getMonth() + 1).toString()).slice(-2);
    const day = ("0" + date.getDate().toString()).slice(-2);
    const hour = ("0" + date.getHours().toString()).slice(-2);
    const minute = ("0" + date.getMinutes().toString()).slice(-2);
    const second = ("0" + date.getSeconds().toString()).slice(-2);
    return date.getFullYear().toString() + "/" + month + "/" + day + " " +
        hour + ":" + minute + ":" + second;
}
export function dateString(epochseconds) {
    const date = new Date(epochseconds);
    const month = ("0" + (date.getMonth() + 1).toString()).slice(-2);
    const day = ("0" + date.getDate().toString()).slice(-2);
    return date.getFullYear().toString() + "/" + month + "/" + day;
}
